<template>
  <div class="accadd">
    <Card>
      <div slot="title">添加账号</div>
      <div slot="content">
        <el-form
          :model="accAddForm"
          :rules="rules"
          ref="accAddForm"
          label-width="100px"
          class="accAdd-form"
        >
          <!-- 登录 -->
          <el-form-item label="账号" prop="account">
            <el-input
              v-model="accAddForm.account"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <!-- 密码 -->
          <el-form-item type="password" label="密码" prop="password">
            <el-input
              type="password"
              v-model="accAddForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <!-- 权限 -->
          <el-form-item label="权限分配" prop="userGroup">
            <el-select v-model="accAddForm.userGroup" placeholder="请选择权限">
              <el-option label="超级管理员" value="super"></el-option>
              <el-option label="普通管理员" value="normal"></el-option>
            </el-select>
          </el-form-item>
          <!-- 登录 -->
          <el-form-item>
            <el-button type="primary" @click="accAddBtn()">添加</el-button>
            <el-button @click="resetFormBtn">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </Card>
  </div>
</template>

<script>
//引入卡片插槽
import Card from "@/components/Card.vue";
//引入验证规则
import { ACC, PWD } from "@/units/regular.js";
//引入账户添加api
import { api_accAdd } from "@/api/acc.js";
export default {
  components: {
    Card,
  },
  data() {
    //用户名正则表达式
    const checkAcc = (rules, value, callback) => {
      if (!value) {
        callback(new Error("用户名不能为空"));
      } else if (!ACC.test(value)) {
        callback(new Error("请输入4-6位数字或字母"));
      } else {
        callback();
      }
    };
    //密码验证
    const checkPwd = (rules, value, callback) => {
      if (!value) {
        callback(new Error("密码不能为空"));
      } else if (!PWD.test(value)) {
        callback(new Error("请输入6-8位数字或字母"));
      } else {
        callback();
      }
    };
    //用户组验证
    // const checkGroup = (rules, value, callback) => {
    //   if (!value) {
    //     callback(new Error("用户组不能为空"));
    //   }
    // };
    return {
      //整个表单的双向绑定的数据
      accAddForm: {
        account: "",
        password: "",
        userGroup: "",
      },
      rules: {
        account: { validator: checkAcc, trigger: "blur" },
        password: { validator: checkPwd, trigger: "blur" },
        userGroup: {
          required: true,
          message: "用户组不能为空",
          trigger: "change",
        },
      },
    };
  },
  methods: {
    accAddBtn() {
      this.$refs.accAddForm.validate(async (valid) => {
        if (valid) {
          //api请求
          let { code, msg } = await api_accAdd(this.accAddForm);
          
            // 跳转到列表页
            this.$router.push("/account/account-list");
          }
        else {
          this.$message.error("验证失败");
        }
      });
    },
    resetFormBtn() {
      this.$refs.accAddForm.resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
.accadd {
  background: #fff;
  border-radius: 4px;
  overflow: hidden;

  .el-form {
    padding: 10px;
    .el-input {
      width: 40%;
    }
  }
}
</style>